import { Responsive, WidthProvider } from 'react-grid-layout'

import { rowHeight, layouts, breakpoints, cols } from '../../layouts/responsive_layout/trading_panel'

import BasicInfoPanel from '../../features/components/trading/BasicInfoPanel'
import OrderActionsPanel from '../../features/components/trading/OrderActionsPanel'
import OrderBookPanel from '../../features/components/trading/OrderBookPanel'
import OrderRecordsPanel from '../../features/components/trading/OrderRecordsPanel'
import QuoteListPanel from '../../features/components/trading/QuoteListPanel'
import TradeRecordsPanel from '../../features/components/trading/TradeRecordsPanel'
import TradingChart from '../../features/components/trading/TradingChartPanel'

const ResponsiveGridLayout = WidthProvider(Responsive)

const TradingPage = () => {

  return (
    <ResponsiveGridLayout className="layout"
      layouts={layouts}
      rowHeight={rowHeight}
      draggableHandle={'.draggable_header'}
      breakpoints={breakpoints}
      isResizable={false}
      cols={cols}>
      <div key="basic_info"><BasicInfoPanel /></div>
      <div key="order_book"><OrderBookPanel /></div>
      <div key="trading_chart"><TradingChart /></div>
      <div key="quote_list"><QuoteListPanel /></div>
      <div key="order_actions"><OrderActionsPanel /></div>
      <div key="trade_records"><TradeRecordsPanel /></div>
      <div key="order_records"><OrderRecordsPanel /></div>
    </ResponsiveGridLayout>
  );
}

export default TradingPage;
