import styled from 'styled-components/macro'
import styles from './togglebtn.module.scss'
import './togglebtn.scss'

export interface ToggleBtnProps {
  left_txt: string
  right_txt: string
  left_value: any
  right_value: any
}

const ToggleBtn = ({ left_txt, right_txt, left_value, right_value }: ToggleBtnProps) => {
  return (
    <>
      <input id="toggle-on" className="toggle toggle-left" name="toggle" value={left_value} type="radio" checked />
      <label htmlFor="toggle-on" className="btn">{left_txt}</label>
      <input id="toggle-off" className="toggle toggle-right" name="toggle" value={right_value} type="radio" />
      <label htmlFor="toggle-off" className="btn">{right_txt}</label>
    </>
  );
};

export default ToggleBtn;
