import { useLocation } from 'react-router-dom'
import { useTranslation } from "react-i18next"

import i18n from 'plugins/i18n'

import { useTheme } from 'styled-components'

import MenuUI from 'components/menus/NormalMenu'

const Menu = () => {

    const { t } = useTranslation()

    const menu_data = [
        {
            "title": t("common:menu_quote"),
            "link": "/",
            "width": 60,
            "submenus": [],
        },
        {
            "title": t("common:menu_trading"),
            "link": "",
            "width": 60,
            "submenus": [
                {
                    "title": t("common:submenu_simple_title"),
                    "desc": t("common:submenu_simple_desc"),
                    "link": "/trading/quick",
                },
                {
                    "title": t("common:submenu_pro_title"),
                    "desc": t("common:submenu_pro_desc"),
                    "link": "/trading/pro",
                }
            ],
        },
        {
            "title": t("common:menu_liquidity"),
            "link": "/liquidity",
            "width": 70,
            "submenus": [],
        },
        {
            "title": t("common:menu_l2wallet"),
            "link": "/l2wallet",
            "width": 80,
            "submenus": [],
        }
    ]


    const location = useLocation()
    
    const theme: any = useTheme()

    const font_family = i18n.language === 'zh_CN' ? 'Source Han Sans CN' : 'Roboto'

    return (
        <MenuUI menu_data={menu_data} location={location} font_family={font_family} submenu_down={theme.imgs.submenu_down} />
    );

}

export default Menu
