import { Link } from 'react-router-dom'

import styled from 'styled-components/macro'
import styles from './menu.module.scss'

const SubMenuContent = styled.div`
display: none;
z-index: 1;
padding-top: 9px;
`

const SubMenuContentInternal = styled.div`
background-color: ${({ theme }) => theme.colors.subMenuBgColor};
min-width: 250px;
margin-left: 20px;
box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
`

export interface SubMenuItemProps {
    active: boolean
};

const SubMenuItem = styled.div<SubMenuItemProps>`
height: 56px;
padding-top: 12px;
padding-left: 10px;
background-color: ${({ active, theme }) => active ? theme.colors.submenu_item_bg_color_a : theme.colors.submenu_item_bg_color_ia};

&:hover {
    background-color:  ${({ theme }) => theme.colors.submenu_item_bg_color_a};
}

.submenu_circle {
    float: left;
    margin-top: 5px;
    background-color: ${({ active, theme }) => active ? theme.colors.submenu_circle_bg_color_a : theme.colors.submenu_circle_bg_color_ia};
    border-radius: 50%;
	width: 8px;
	height: 8px;
}

.submenu_title {
    color: ${({ active, theme }) => active ? theme.colors.submenu_title_color_a : theme.colors.submenu_title_color_ia};
    margin-left: 17px;
    font-size: 14px;
    line-height: 18px;
}

.submenu_desc {
    padding-left: 17px;
    color: ${({ theme }) => theme.colors.submenu_desc_color};
    font-size: 12px;
    line-height: 16px;
    text-shadow: ${({ active, theme }) => active ? theme.effects.submenu_desc_text_shadow : ''};
}

&:hover .submenu_circle {
    background-color: ${({ theme }) => theme.colors.submenu_circle_bg_color_a};
}

&:hover .submenu_title {
    color:  ${({ theme }) => theme.colors.submenu_title_color_a};
}

&:hover .submenu_desc {
    text-shadow: ${({ theme }) => theme.effects.submenu_desc_text_shadow};
}

`

export interface SubMenuProps {
    submenus: any
    location: any
}

const SubMenu = ({ submenus, location, }: SubMenuProps) => {

    return (<SubMenuContent className={styles.dropdown_content}>
        <SubMenuContentInternal>
            {
                submenus.map((sub_value: any, sub_index: number) => {
                    const isActive = location.pathname === sub_value['link']
                    return (
                        <Link to={sub_value['link']} key={sub_index}>
                            <SubMenuItem active={isActive}>
                                <div className='submenu_circle'></div>
                                <div className='submenu_title'>{sub_value['title']}</div>
                                <div className='submenu_desc'>{sub_value['desc']}</div>
                            </SubMenuItem>
                        </Link>
                    )
                })
            }
        </SubMenuContentInternal>
    </SubMenuContent>)
}

export default SubMenu